/**
 *  self.name Worker 
    self.onmessagemessage
    self.onmessageerror messageerror 
    self.close() Worker 
    self.postMessage() Worker 
    self.importScripts() JS 
 */
/* global Worker */
const webWorkObj = {
/**
     * @param url: webworker
     * @param options: name  webworker
     * @param callback:webwork
     */
  createWebWorker: (url, options, callback) => {
    let instance;
    if (typeof Worker !== 'undefined') {
      instance = new Worker(url, options);
    } else {
      console.log('Sorry, your browser does not support Web Workers...');
    }
    if (instance) {
      instance.onmessage = (event) => {
        callback(null, event);
        instance.terminate();
      };
      instance.onerror = (event) => {
        console.error([
          'ERROR: Line ', event.lineno,
          ' in ', event.filename, ': ',
          event.message,
        ].join(''));
        callback(event, null);
        instance.terminate();
      };
    }
    return instance;
  },
  stopWorker(instance) {
    instance.terminate();
  },
};

