import { historyCount } from '../config';

export default class History {
  constructor(afterTriggerHistoryAction) {
    this.afterTriggerHistoryAction = afterTriggerHistoryAction;
    this.undoItems = [];
    this.redoItems = [];
  }

  // 
  add(data, actionParams = {}) {
    const { actionType } = actionParams;
    // 
    if (actionType === 'no-record') {
      return false;
    }
    // linjian    paste-action-${type}
    const useDirectlyArr = ['insert-row', 'delete-row', 'modify-data', 'delete-text', 'paste-action-cut', 'paste-action-copy', 'paste-action-autofill'];
    if (useDirectlyArr.includes(actionType)) {
      const tempItem = JSON.stringify(actionParams);
      // linjian   
      if (typeof this.afterTriggerHistoryAction === 'function') {
        this.afterTriggerHistoryAction('history-add', actionParams);
      }
      this.undoItems.push(tempItem);
    } else {
      this.undoItems.push(JSON.stringify(data));
    }
    this.redoItems = [];
    // linjian 
    if (this.undoItems.length >= historyCount) {
      this.undoItems.shift(); // 
    }
    return true;
  }

  canUndo() {
    return this.undoItems.length > 0;
  }

  canRedo() {
    return this.redoItems.length > 0;
  }

  // ctri + z
  undo(currentd, cb) {
    const { undoItems, redoItems } = this;
    if (this.canUndo()) {
      const tempItem = undoItems.pop();
      const undoItem = JSON.parse(tempItem);
      // linjian actionType
      const { actionType } = undoItem;
      if (actionType) {
        redoItems.push(tempItem);
        cb(undoItem);
        // linjian   
        if (typeof this.afterTriggerHistoryAction === 'function') {
          this.afterTriggerHistoryAction('history-undo', undoItem);
        }
      } else {
        redoItems.push(JSON.stringify(currentd));
        cb(undoItem);
      }
    }
  }

  // ctri + y
  redo(currentd, cb) {
    const { undoItems, redoItems } = this;
    if (this.canRedo()) {
      const tempItem = redoItems.pop();
      const redoItem = JSON.parse(tempItem);
      const { actionType } = redoItem;
      if (actionType) {
        undoItems.push(tempItem);
        cb(redoItem);
        // linjian   
        if (typeof this.afterTriggerHistoryAction === 'function') {
          this.afterTriggerHistoryAction('history-redo', redoItem);
        }
      } else {
        undoItems.push(JSON.stringify(currentd));
        cb(redoItem);
      }
    }
  }
}
